import { NgModule } from '@angular/core';
import { EqualValidator } from './validate-equal.directive';
import { LimitInputDirective } from './limit-to.directive';
import { TranslateModule, TranslateLoader, TranslateService } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { HttpModule, Http } from '@angular/http';



// AoT requires an exported function for factories
export function HttpLoaderFactory(http: Http) {
   return new TranslateHttpLoader(http, 'assets/i18n/', '.json');
}

@NgModule({
    imports: [
        TranslateModule.forRoot({
         loader: {
           provide: TranslateLoader,
           useFactory: HttpLoaderFactory,
           deps: [Http],
         },
         isolate: false
       })
    ],
    declarations: [EqualValidator, LimitInputDirective],
    providers: [],
    exports: [
        EqualValidator,
        LimitInputDirective,
        TranslateModule,
    ]
})
export class SharedModule {
    constructor(private translate: TranslateService) {
                  
        translate.addLangs(["gb", "fr", "be", "de", "es", "nl", "it"]);
        translate.setDefaultLang('gb');

        let browserLang = translate.getBrowserLang();
        translate.use(browserLang.match(/en|fr/) ? browserLang : 'gb');
    }
}


