import { Component, OnInit } from '@angular/core';
import { TranslateService, LangChangeEvent } from '@ngx-translate/core';
// 
@Component({
    selector: 'i18n-translate',
    template: `
    <li class="nav-item dropdown" dropdown>
      <a href class="nav-link dropdown-toggle" dropdownToggle (click)="false">
        <span class=" lang"><span class="flag-icon flag-icon-{{language.key}}"></span><span class="languages">{{language.value}}</span></span>
      </a>
      <div class="dropdown-menu dropdown-menu-right" *dropdownMenu aria-labelledby="simple-dropdown">
       <a *ngFor="let lang of languages" (click)="translate.use(lang.key); $event.preventDefault();" class="dropdown-item" href="#"><span class="flag-icon flag-icon-{{ lang.key }}"></span><span  class="languages">{{ lang.value }}</span></a>
      </div>
    </li>
    `
})
export class I18NComponent implements OnInit {
    public status: { isopen: boolean } = { isopen: false };
    language: language;
    languages: language[];
    browserLang: string;
    constructor(public translate: TranslateService) {
        this.browserLang = translate.getBrowserLang();
    
        this.translate.onLangChange.subscribe((event: LangChangeEvent) => {
            this.setCurrentLanguage(event.lang);
        });

        this.languages = [
            {
                key: "fr",
                value: "francais"
            },
            {
                key: "gb",
                value: "Anglais"
            },
            {
                key: "be",
                value: "Flamand"
            },
            {
                key: "de",
                value: "Allemand"
            },
            {
                key: "nl",
                value: "Néerlandais"
            },
            {
                key: "it",
                value: "Italien"
            }

        ]
    }

    public toggleDropdown($event: MouseEvent): void {
        $event.preventDefault();
        $event.stopPropagation();
        this.status.isopen = !this.status.isopen;
    }

    ngOnInit(): void {
       this.setCurrentLanguage(this.browserLang);
    }

    setCurrentLanguage(language: string): void {
        this.languages.forEach(element => {
            console.log(this.browserLang);
            if (element.key === language) {
                this.language = {
                    key : element.key,
                    value : element.value 
                };
            }
        });
    }

}
interface language {
  key: string;
  value: string;
}


